import merge from 'deepmerge';
import { mergedResolvers } from './index';
const { GraphQLScalarType, Kind } = require('graphql');

const dateScalar = new GraphQLScalarType({
  name: 'Date',
  description: 'Date custom scalar type',
  serialize(value: { getTime: () => unknown }) {
    return value.getTime(); // Convert outgoing Date to integer for JSON
  },
  parseValue(value: string | number | Date) {
    return new Date(value); // Convert incoming integer to Date
  },
  parseLiteral(ast: { kind: unknown; value: string }) {
    if (ast.kind === Kind.INT) {
      return new Date(parseInt(ast.value, 10)); // Convert hard-coded AST string to integer and then to Date
    }
    return null; // Invalid hard-coded value (not an integer)
  },
});

const obj3 = { ...mergedResolvers, ...dateScalar };

const allResolvers = [...Object.values(mergedResolvers)];

let resolversObj = {};

allResolvers.forEach((resolver) => {
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  resolversObj = merge.all([resolversObj, resolver as any]);
});

export default resolversObj;
